.App {
  width: 100vw;
  height: 100vh;
}
#pic-container {
  position: relative;
  @apply w-fit h-fit;
}

#hover{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
}

#glowing{
  @apply w-10 h-10 ;
  @apply bg-gradient-to-bl from-purple-600 to-purple-100 rounded-full;
  position: absolute;
  top: var(--mouse-y);
  left: var(--mouse-x);
  opacity: 0;
  z-index: 20;
  /*make it glow*/
  box-shadow: 0 0 20px 5px rgba(255, 255, 255, 0.7);
  transition: opacity 0.5s ease;
}

#highlight{

  background: radial-gradient(circle at var(--mouse-x) var(--mouse-y),transparent 0%, black 40%, black 100%), url('https://cdn.midjourney.com/c8305050-2eb6-401e-93c9-3e239a958fad/grid_0.png');
  background-position: center;
  background-size: cover;
  opacity: 0;
  transition: opacity 0.5s ease;
}
.highlight{
  position: absolute;
  @apply rounded-md;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* z-index: 10; */
  background: radial-gradient(circle at var(--mouse-x) var(--mouse-y),rgba(255,255,255,0.1) 0%, transparent 100%);
  border-radius: inherit;
}



.resume.link{
  @apply text-white;
  @apply font-bold;
}
.resume.btn{
  @apply rounded-xl;
  @apply bg-dark-300 p-4 ;
  @apply drop-shadow-xl;
  position: relative;

}

.website{
  @apply w-full h-auto rounded-md -z-10;
}

.name{
  @apply text-center w-11;
}

.overview{
  @apply w-[50%] h-auto m-auto;
}
